<template>
  <section class="myPage">
    <div class="container">
      <div class="pic">
        <img :src="this.settingImage" width="300" height="300" />
        <br />
        <div class="buttons">
          <input type="file" @change="onDrop" />
          <br />
          <button class="btn btn-info" @click="update"></button>
        </div>
      </div>
      <div class="contents">
        <div class="text">
          <p>{{profile.comment}}</p>
        </div>
      </div>
    </div>
    <div class="main">
      <p>UID:</p>
      <input type="text" class="form-control" v-model="profile.userId" disabled="true" />
      <p>Name</p>
      <input type="text" class="form-control" v-model="profile.loginUser" />
      <p>Self-introduction:</p>
      <textarea
        type="textarea"
        class="form-control"
        v-model="profile.introduction"
        maxlength="200"
        rows="3"
      ></textarea>
      <p>Comment</p>
      <input type="text" maxlength="50" class="form-control" v-model="profile.comment" />
      <button class="btn btn-info" @click="updateMyPage"></button>
    </div>
  </section>
</template>

<script>
import firebase from "firebase";

export default {
  name: "myPage",
  props: ["profile"],
  data: function () {
    return {
      fileList: "",
      uploadedImage: "",
      settingImage: this.profile.profileImg,
      isDropingFlg: false, //
    };
  },

  created: function () {},

  methods: {
    onDrop(event) {
      this.isDropingFlg = true;
      //
      let fileList = event.target.files || event.dataTransfer.files;
      this.fileList = fileList;

      this.createImage(fileList[0]);
    },
    // 
    createImage(file) {
      let reader = new FileReader();
      reader.onload = (e) => {
        this.settingImage = e.target.result;
      };
      reader.readAsDataURL(file);
    },
    update() {
      if (this.isDropingFlg) {
        //
        var errFlg;
        let blob;
        if (this.fileList[0].type == "image/png") {
          blob = new Blob(this.fileList, { type: "image/png" });
          errFlg = false;
        } else if (this.fileList[0].type == "image/jpg") {
          blob = new Blob(this.fileList, { type: "image/jpg" });
          errFlg = false;
        } else if (this.fileList[0].type == "image/jpeg") {
          blob = new Blob(this.fileList, { type: "image/jpeg" });
          errFlg = false;
        } else if (this.fileList[0].type == "image/gif") {
          blob = new Blob(this.fileList, { type: "image/gif" });
          errFlg = false;
        } else {
          errFlg = true;
        }

        if (!errFlg) {
          this.updateImage(blob);
        }
      }
    },
    updateImage(blob) {
      var storageRef = firebase.storage().ref();
      var mountainsRef = storageRef.child(`img/${this.fileList[0].name}`);
      //Firebase 
      mountainsRef.put(blob).then(() => {
        //
        mountainsRef
          .getDownloadURL()
          .then((url) => {
            //URL
            this.updateAccount(url);
          })
          .catch((error) => {
            console.log(error);
          });
      });
    },
    /**
     * Cloud FireStore
     */
    updateAccount(url) {
      var user = firebase.auth().currentUser;
      user
        .updateProfile({
          photoURL: url,
        })
        .then(() => {
          this.$emit("updateProfileImg", user.photoURL);
          alert("");
        })
        .catch(() => {
          alert("");
        });
    },

    /**
     *Cloud Firestore
     */
    updateMyPage: function () {
      //Cloud Firestore
      firebase
        .firestore()
        .collection("profile")
        .doc(this.profile.userId)
        .set({
          uid: this.profile.userId,
          userName: this.profile.loginUser,
          introduction: this.profile.introduction,
          comment: this.profile.comment,
        })
        .then(() => {
          alert("");
          console.log(`update favorites!`);
        })
        .catch((error) => {
          console.log(error);
        });
    },
  },
};
</script>

