<template>
  <div class="container mt-5">
    <h2></h2>
    <br />
    <span class="errorMsg">{{errorMsg}}</span>
    <br />
    <div class="row">
      <div class="col-sm-8">
        <form @submit.prevent="signUp">
          <div class="form-group">
            <label for="displayName"></label>

            <input
              type="text"
              class="form-control"
              placeholder=""
              v-model="username"
              autocomplete="off"
            />
          </div>

          <div class="form-group">
            <label for="email"></label>
            <input type="email" class="form-control" id="email" v-model="email" autocomplete="off" />
          </div>
          <div class="form-group">
            <label type="password"></label>
            <input type="password" class="form-control" id="password" v-model="password" />
          </div>
          <div class="form-group">
            <label type="password">()</label>
            <input type="password" class="form-control" id="password" v-model="password2" />
          </div>
          <button type="submit" class="btn btn-info"></button>
        </form>
        <p>
          <router-link to="/"></router-link>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
// import defaultImg from "@/assets/cat.png";

export default {
  name: "singUp",
  props: [],
  data: function () {
    return {
      email: "",
      password: "",
      password2: "",
      username: "",
      isAuthenticated: false,
      errorMsg: "",
    };
  },

  mounted: function () {
    this.errorMsg = "";
  },

  methods: {
    /**
     * Cloud Firestore
     * email password displayName photoURL
     */
    signUp: function () {
      //
      if (!this.checkForm()) {
        return;
      }

      //Cloud Firestore
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.email, this.password)
        .then((result) => {
          result.user.updateProfile({
            displayName: this.username,
            photoURL: require("@/assets/otter.jpg"),
          });
          alert("");
          this.$router.push("/");
        })
        .catch((error) => {
          this.errorMsg = `${error.message}`;
        });
    },

    /**
     * 
     * @return {boolean}
     */
    checkForm: function () {
      if (this.username.length < 2) {
        this.errorMsg = "2";
        return false;
      }

      if (this.email === "") {
        return false;
      }

      if (this.password.length < 6) {
        this.errorMsg = "6";
        return false;
      }

      if (this.password !== this.password2) {
        this.errorMsg = "";
        return false;
      }

      return true;
    },
  },
};
</script>

<style scoped>
.container {
  padding-top: 200px;
}

.errorMsg {
  color: red;
  font-size: 28px;
}
</style>
