<template>
  <div class="container mt-5">
    <h2></h2>
    <br />
    <template v-if="errorMsg !== ''">
      <span class="errorMsg">{{errorMsg}}</span>
      <br />
      <br />
    </template>
    <div class="row">
      <div class="col-sm-8">
        <form @submit.prevent="updatePassword">
          <div class="form-group">
            <label for="email"></label>
            <input
              type="email"
              class="form-control"
              id="email"
              v-model="currentMail"
              disabled="true"
            />
          </div>
          <div class="form-group">
            <label for="email"></label>
            <input type="email" class="form-control" id="email" v-model="email" autocomplete="off" />
          </div>
          <button type="submit" class="btn btn-info"></button>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";

export default {
  name: "updateMail",
  props: [],
  data: function () {
    return {
      currentMail: "",
      email: "",
      isAuthenticated: true,
      errorMsg: "",
    };
  },

  mounted: function () {
    var user = firebase.auth().currentUser;
    this.currentMail = user.email;
  },

  methods: {
    /**
     * 
     */
    updatePassword: function () {
      //
      if (this.checkForm()) {
        var user = firebase.auth().currentUser;

        // Clouod FireStore
        user
          .updateEmail(this.email)
          .then(() => {
            console.log("");
            alert("");
            this.$router.push({ path: "/" });
          })
          .catch(() => {
            alert("");
          });

        //
        // user
        //   .sendEmailVerification(this.email)
        //   .then(function () {
        //     alert(`${this.email}`);
        //   })
        //   .catch(function (error) {
        //     console.log(error);
        //   });
      }
    },

    /**
     * 
     * @return {boolean}
     */
    checkForm: function () {
      if (this.email === "") {
        this.errorMsg = "";
        return false;
      }
      return true;
    },
  },
};
</script>

