  <template>
  <div class="chatbot">
    <message v-bind:chatList="chatList" v-bind:userId="this.userId"></message>
    <textForm v-on:submit="postMessage"></textForm>
  </div>
</template>

<script>
import message from "./message.vue";
import textForm from "./textForm.vue";
import mixin from "../../mixins.js";

export default {
  name: "chat",
  props: ["userId", "loginUser", "profileImg"],
  components: {
    message: message,
    textForm: textForm,
  },

  data: function () {
    return {
      chatList: [
        {
          id: "0001",
          userId: "kawausootterbot",
          name: "chang-kawauso",
          img: require("@/assets/img/otter.jpg"),
          message: "",
          date: this.getCurrentDate(),
        },
      ],
      replyMsgMap: new Map(),
    };
  },

  mixins: [mixin],
  mounted: function () {
    //
    this.replyMsgMap.set("", "");
    this.replyMsgMap.set("", "");
    this.replyMsgMap.set("", "");
    this.replyMsgMap.set("kick", "");
    this.replyMsgMap.set("", "");
    this.replyMsgMap.set("kimochi", "");
  },

  methods: {
    /**
     * 
     * 
     */
    postMessage: function (input) {
      if (input.length > 200 || input.length === 0) {
        return;
      }

      const id = this.chatList.length + 1;
      const name = this.loginUser;
      const img = this.profileImg;

      //
      this.chatList.push({
        userId: this.userId,
        id: id,
        name: name,
        img: img,
        message: input,
        date: this.getCurrentDate(),
      });
    },

    /**
     * 
     * @param key 
     */
    replyMsg: function (key) {
      const id = this.chatList.length + 1;
      const name = "chang-kawauso";

      //inputmap
      const reply = this.replyMsgMap.get(key);

      //
      this.chatList.push({
        id: id,
        name: name,
        img: require("@/assets/img/otter.jpg"),
        message: reply,
        date: this.getCurrentDate(),
      });
    },
  },

  watch: {
    chatList: function () {
      //
      const lastMsg = this.chatList[this.chatList.length - 1].message;
      const name = this.chatList[this.chatList.length - 1].name;

      //
      if (name === "chang-kawauso") {
        return;
      }

      //
      //map
      setTimeout(() => {
        if (
          lastMsg.indexOf("") != -1 ||
          lastMsg.indexOf("") != -1 ||
          lastMsg.indexOf("") != -1
        ) {
          this.replyMsg("");
          return;
        }
        if (
          lastMsg.indexOf("") != -1 ||
          lastMsg.indexOf("") != -1 ||
          lastMsg.indexOf("") != -1 ||
          lastMsg.indexOf("") != -1 ||
          lastMsg.indexOf("unko") != -1 ||
          lastMsg.indexOf("") != -1
        ) {
          this.replyMsg("");
          return;
        }
        if (
          lastMsg.indexOf("") != -1 ||
          lastMsg.indexOf("") != -1 ||
          lastMsg.indexOf("kawauso") != -1 ||
          lastMsg.indexOf("otter") != -1 ||
          lastMsg.indexOf("") != -1 ||
          lastMsg.indexOf("") != -1 ||
          lastMsg.indexOf("") != -1 ||
          lastMsg.indexOf("") != -1
        ) {
          this.replyMsg("");
          return;
        }
        if (lastMsg.indexOf("chang") != -1 || lastMsg.indexOf("") != -1) {
          this.replyMsg("kick");
          return;
        }
        if (
          lastMsg.indexOf("") != -1 ||
          lastMsg.indexOf("") != -1 ||
          lastMsg.indexOf("") != -1
        ) {
          this.replyMsg("");
          return;
        }
        this.replyMsg("kimochi");
      }, 2000);

      //
      setTimeout(() => {
        var element = document.querySelector("div.chatbot");
        element.scrollTop = element.scrollHeight;
      }, 2800);
    },
  },
};
</script>

