<template>
  <div class="roomVue">
    <div id="profileModal" v-bind:class="{'hidden':!isOpenProfileModal}">
      <ul>
        <li @click="activeItem = 99;isOpenProfileModal = !isOpenProfileModal">
          <a></a>
        </li>
        <li>
          <router-link to="/updateMail"></router-link>
        </li>
        <li>
          <router-link to="/updatePass"></router-link>
        </li>
      </ul>
    </div>
    <div
      id="mask"
      v-bind:class="{'hidden':!isOpenProfileModal}"
      @click="isOpenProfileModal = !isOpenProfileModal"
    ></div>
    <!-- RoomKey -->
    <!-- roomModal.vuemountedv-if -->
    <template v-if="isOpenModal">
      <roomModal
        v-bind:roomKey="roomKey"
        v-bind:errorMsg="errorMsg"
        v-on:isopenmodalchanged="isOpenModal = !isOpenModal"
        v-on:set-errormsg="errorMsg = ''"
        v-on:open-room="enterRoomForPrivateChat"
      ></roomModal>
    </template>
    <div class="rooms">
      <aside>
        <div class="aside-head">
          <h2>Rooms</h2>
          <p>
            <span id="mark"></span>
            <a
              href="#"
              class="myPage"
              @click="isOpenProfileModal = !isOpenProfileModal"
            >{{ loginUser }}</a>
          </p>
          <div class="box">
            <div class="pic">
              <img
                v-bind:src="profile.profileImg"
                @click="isOpenProfileModal = !isOpenProfileModal"
              />
            </div>
            <div class="contentsForRoom">
              <div class="textForRoom">
                <p>{{profile.comment}}</p>
              </div>
            </div>
          </div>
        </div>
        <div class="aside-body">
          <section>
            <div
              v-for="(menu, index) in menuList"
              @click="activeItem = index"
              v-bind:key="menu.number"
              v-bind:class="{ 'clicked': activeItem === index }"
            >{{ menu.name }}</div>
          </section>
        </div>
      </aside>

      <main>
        <div class="main-head">
          <template v-if="activeItem !== ''">
            <template v-if="activeItem == 99">
              <h2>My Page</h2>
            </template>
            <template v-else>
              <h2>{{ menuList[activeItem].name }}</h2>
              <div>
                <b-form-group>
                  <b-form-radio-group
                    id="btn-radios-1"
                    v-model="selected"
                    :options="options"
                    buttons
                    name="radios-btn-default"
                  ></b-form-radio-group>
                </b-form-group>
              </div>
            </template>
          </template>
        </div>
        <div class="main-body">
          <!-- Room -->
          <roomCreation
            v-show="activeItem == 0"
            v-bind:errorMsg="this.errorMsg"
            v-bind:loginUser="this.loginUser"
            v-bind:userId="this.userId"
          ></roomCreation>

          <!-- Room -->
          <roomList
            v-show="activeItem == 1"
            v-bind:filteredRooms="this.filteredRooms"
            v-on:open-modal="openModal"
            v-on:send-to-chat="sendToChat"
            v-on:chageFavorites="chageFavorites"
          ></roomList>

          <!-- My Room -->
          <myRoom
            v-show="activeItem == 2"
            v-bind:filteredRooms="this.filteredRooms"
            v-bind:userId="this.userId"
            v-on:open-modal="openModal"
            v-on:send-to-chat="sendToChat"
            v-on:chageFavorites="chageFavorites"
            v-on:deleteRoom="deleteRoom"
          ></myRoom>

          <!-- favorites -->
          <favRoom
            v-show="activeItem == 3"
            v-bind:filteredRooms="this.filteredRooms"
            v-on:open-modal="openModal"
            v-on:send-to-chat="sendToChat"
            v-on:chageFavorites="chageFavorites"
          ></favRoom>

          <!-- chatbot -->
          <section v-show="activeItem == 4" class="chatbot">
            <chatbot
              v-bind:userId="this.userId"
              v-bind:loginUser="loginUser"
              v-bind:profileImg="profile.profileImg"
            ></chatbot>
          </section>

          <!--  -->
          <myPage
            v-show="activeItem == 99"
            v-bind:profile="profile"
            v-on:updateProfileImg="updateProfileImg"
          ></myPage>
        </div>
      </main>
    </div>
    <footer></footer>
  </div>
</template>

<script>
import firebase from "firebase";
import mixin from "../mixins.js";
import chatbot from "./chat/chatbot.vue";
import roomList from "./room/roomList.vue";
import myRoom from "./room/myRoom.vue";
import favRoom from "./room/favRoom.vue";
import roomCreation from "./room/roomCreation.vue";
import myPage from "./myPage/myPage.vue";
import roomModal from "./roomModal.vue";

export default {
  name: "room",
  props: [],
  components: {
    chatbot: chatbot,
    roomList: roomList,
    myRoom: myRoom,
    favRoom: favRoom,
    roomCreation: roomCreation,
    myPage: myPage,
    roomModal: roomModal,
  },
  data: function () {
    return {
      rooms: [],
      menuList: [
        {
          number: 0,
          name: "Create",
        },
        {
          number: 1,
          name: "Rooms",
        },
        {
          number: 2,
          name: "My Room",
        },
        {
          number: 3,
          name: "Favorites",
        },
        {
          number: 4,
          name: " To Question Chang",
        },
      ],
      isOpenModal: false,
      isOpenProfileModal: false,
      // showRoomCreationFormFlag: false,
      roomKey: "",
      errorMsg: "",
      loginUser: this.$route.query.loginUser,
      currentRoom: "",
      activeItem: 1,
      userId: this.$route.query.userId,
      fav_roomIds: "",
      options: [
        { text: "All", value: 0 },
        { text: "Private", value: 1 },
        { text: "Open", value: 2 },
      ],
      selected: [0],
      boforeSortRooms: [],
      profile: {
        profileImg: this.$route.query.profileImg,
        loginUser: this.$route.query.loginUser,
        userId: this.$route.query.userId,
        introduction: "",
        comment: "",
      },
    };
  },
  mixins: [mixin],

  created: function () {
    //
    this.isOpenModal = false;
    this.roomKey = "";
    this.errorMsg = "";
    //rooms
    this.getRoom();

    //profile
    firebase
      .firestore()
      .collection("profile")
      .doc(this.userId)
      .get()
      .then((doc) => {
        if (doc.exists) {
          this.profile.introduction = doc.data().introduction;
          this.profile.comment = doc.data().comment;
        } else {
          console.log("profile");
          return;
        }
      })
      .catch((error) => {
        console.log(error.message);
        return;
      });
  },

  methods: {
    /**
     * Cloud Firestoreroom
     */
    getRoom: function () {
      let rooms = [];

      //
      let promise = new Promise((resolve) => {
        firebase
          .firestore()
          .collection("rooms")
          .orderBy("last_modified")
          .onSnapshot((snap) => {
            snap.docChanges().forEach((change) => {
              if (change.type === "added") {
                const room = change.doc.data();
                room.addFlg = false;
                rooms.push(room);
                console.log("room");
              }
            });
            //TODO
            //
            promise.then(() => {
              setTimeout(() => {
                this.rooms = rooms.sort(function (a, b) {
                  return a.last_modified < b.last_modified ? 1 : -1;
                });
                console.log("");
              }, 400);
            });
            resolve();
          });
      });
    },

    /**
     * Cloud FirestoreroomId
     * roomaddFlg
     */
    setFavRooms: function () {
      //
      let fav_roomIds = [];

      //id
      firebase
        .firestore()
        .collection("favorites")
        .doc(this.userId)
        .get()
        .then((doc) => {
          if (doc.exists) {
            fav_roomIds = doc.data().fav_roomIds;
            console.log("fav_roomIds");

            //data
            this.fav_roomIds = fav_roomIds;
          } else {
            console.log("fav_rooms");
            return;
          }
        })
        .catch((error) => {
          console.log(error.message);
          return;
        });

      //fav_roomIdsroomstrue
      setTimeout(() => {
        this.rooms.forEach((room) => {
          if (this.fav_roomIds.includes(room.roomId)) {
            room.addFlg = true;
          }
        });
        console.log("room");
      }, 500);
    },

    /**
     * chat
     * userId,loginUser,profileImg,roomId
     */
    sendToChat: function (room) {
      this.$router.push({
        path: "/chat",
        query: {
          userId: this.userId,
          loginUser: this.loginUser,
          profileImg: this.profile.profileImg,
          roomId: room.roomId,
          roomName: room.roomName,
          rooms: this.rooms,
        },
      });
    },

    /**
     * chat(privateChat)
     * userId,loginUser,profileImg,roomId
     */
    enterRoomForPrivateChat: function () {
      //
      this.errorMsg = "";
      //
      this.sendToChat(this.currentRoom);
    },

    /**
     *roomkey
     */
    checkRoomKey: function () {
      if (this.inputKey !== this.roomKey) {
        return false;
      }
      this.roomKey = "";
      return true;
    },

    /**
     *roomKey
     * @param room 
     */
    openModal: function (room) {
      //
      this.currentRoom = room;
      //roomKey
      this.roomKey = room.roomKey;

      this.errorMsg = "";

      //roomroomKey
      if (this.roomKey === "") {
        alert("system error");
        return;
      }

      //
      this.isOpenModal = !this.isOpenModal;

      //
      this.scrollUp();
    },

    /**
     * 
     */
    scrollUp: function () {
      window.scrollTo(0, 0);
    },

    /**
     * 
     * @param event 
     * @param room room
     */
    chageFavorites: function (event, room) {
      //t
      event.stopPropagation();

      //room
      room.addFlg = !room.addFlg;

      //Cloud FireStore
      this.updateFavorites();
    },

    /**
     * Cloud Firestore
     * roomroomId
     */
    updateFavorites: function () {
      let fav_rooms = [];

      //DBroomId
      //1,room
      fav_rooms = this.rooms.filter((room) => {
        return room.addFlg === true;
      });
      //2,roomId
      this.fav_roomIds = fav_rooms.map((room) => {
        return room.roomId;
      });

      //Cloud Firestore
      firebase
        .firestore()
        .collection("favorites")
        .doc(this.userId)
        .set({
          fav_roomIds: this.fav_roomIds,
          uid: this.userId,
          useName: this.loginUser,
        })
        .then(() => {
          console.log(`update favorites!`);
        })
        .catch((error) => {
          console.log(error);
        });
    },

    /**
     * room
     * @param event 
     * @param room room
     */
    deleteRoom: function (event, room) {
      //
      event.stopPropagation();

      if (!window.confirm(`${room.roomName}`)) {
        return;
      }

      //room
      firebase
        .firestore()
        .collection("rooms")
        .doc(room.roomId)
        .delete()

        .then(function () {
          console.log(`${room.roomName} delteed!`);
        })
        .catch((error) => {
          console.log(error);
        });

      //
      this.getRoom();
      this.setFavRooms();
    },

    updateProfileImg: function (newPhotoURL) {
      this.profile.profileImg = newPhotoURL;
      console.log("");
    },
  },

  watch: {
    rooms: function () {
      //
      this.setFavRooms();

      setTimeout(() => {
        this.selected = 0;
      }, 600);
    },
  },

  computed: {
    filteredRooms: function () {
      let filteredRooms = [];

      //all
      if (this.selected === 0) {
        return this.rooms;
      }
      //private
      if (this.selected === 1) {
        this.rooms.forEach((room) => {
          if (room.roomKey !== "") {
            filteredRooms.push(room);
          }
        });
      }
      //open
      if (this.selected === 2) {
        this.rooms.forEach((room) => {
          if (room.roomKey === "") {
            filteredRooms.push(room);
          }
        });
      }

      return filteredRooms;
    },
  },
};
</script>


